
.pagenation{width:450px;margin:30px auto
}
.pagenation ul li{list-style-type:none;float:left
}
.pagenation ul li a.active{background-color:#ff7b6c;color:white
}
.pagenation a.pages{display:inline-block;float:left;text-decoration:none;border:1px solid #ff7b6c;padding:3px 8px;margin-right:5px;border-radius:10px;color:#ff7b6c;cursor:pointer;font-size:16px
}
.pagenation a.disabled{cursor:not-allowed
}
.pagenation .go{clear:both;padding-top:15px;width:300px;color:#aaa
}
.pagenation .go .pages{position:relative;float:none
}
.pagenation .goPage{width:50px;border:1px solid #ff7b6c;padding:3px 8px;outline:none;border-radius:10px;line-height:25px;color:#aaa;text-align:center;font-size:15px
}
.pagenation input::-webkit-outer-spin-button,.pagenation input::-webkit-inner-spin-button{-webkit-appearance:none !important;margin:0
}
@media screen and (max-width: 500px){
.pagenation{width:80%
}
.pagenation .pre{margin-bottom:10px
}
.pagenation ul{width:100%;float:left
}
.pagenation .next{display:block;margin-top:10px
}
.pagenation a.pages{font-size:12px
}
}



