
.show-story{margin:30px auto;width:80%;color:#8d6e63;box-shadow:0 0 15px 0 #CCC;background:#F6F4EC;border-radius:3px;padding:0 10px;padding-bottom:40px;font:14px/2.5 "Microsoft YaHei"
}
.story-tag{width:81%;margin:auto;margin-top:10px
}
.story-tag .tag-item:first-child{margin-left:0
}
.story-tag .tag-item a{background:#F6F4EC;box-shadow:0 0 15px 0 #CCC;color:#8d6e63;border-color:#8d6e63
}
@media screen and (max-width: 500px){
.show-story{width:95%
}
.story-tag .tag-item{font-size:14px;margin:20px 2px
}
.story-tag .tag-item a{padding:10px 10px
}
}



