
.s-item{display:inline-block;width:500px;height:105px;margin:20px;background-color:#fff;border:1px solid #eee;box-shadow:0 1px 4px rgba(0,0,0,0.27),0 0 40px rgba(0,0,0,0.06) inset;position:relative;*zoom:1;overflow:hidden;cursor:pointer
}
.s-item .left{float:left;width:100px
}
.s-item .left img{height:100px
}
.s-item .right{float:left;width:335px;margin-left:60px
}
.s-item .right .title{font-size:16px;color:#62bbc3
}
.s-item .right p{font-size:13px;color:#aaa
}
@media screen and (max-width: 500px){
.s-item{width:100%;margin:5px
}
.s-item .left{float:left;width:30%
}
.s-item .left img{width:100%
}
.s-item .right{width:70%;margin:0px
}
.s-item .right .title{margin-left:2%
}
.s-item .right p{margin-left:2%;width:95%
}
}



